.background {
  background-image: url("../../public/images/back.jpg");

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-size: cover;

  animation: background-animation 15s linear infinite;
  object-fit: cover;
}

@keyframes background-animation {
  0% {
    background-image: url("../../public/images/back.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
  }
  25% {
    background-image: url("../../public/images/back.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
  }
  50% {
    background-image: url("../../public/logo512.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
  }
  75% {
    background-image: url("../../public/images/back.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
  }
  100% {
    background-image: url("../../public/logo192.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: cover;
  }
}
